import { useEffect, useState } from "react";
import { loadAuth, storeAuth } from "../model/SecureChatStorage";
import LoadingPage from "./LoadingPage";
import {login} from "../api/ChatApi";

function LoginPage({ auth, setAuth, ...props }) {

  //   
  const [loading, setLoading] = useState(true)

  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')
  const [rememberMe, setRememberMe] = useState(false)

  //    ,
  //        
  useEffect(() => {

    //        
    const auth = loadAuth()
    //   ,  
    if (auth) {
      setAuth(auth)
    }
    //  
    setLoading(false)
  }, [auth, setAuth])

  //      
  const onLogin = async () => {
    //    
    setLoading(true)

    //      
    setUsername('')
    setPassword('')
    //      
    const auth = await login({username, password})
    //    
    if (auth) {
      //   
      setAuth(auth)
      if (rememberMe) {
        //    rememberMe
        //       
        storeAuth(auth)
      }
    }
    //  
    setLoading(false)
  }

  //   ,   
  if (loading) {
    return <LoadingPage/>
  }

  //       /
  return <div className="login-page">
    <p className="login-line"><input className="login-input"
      value={username}
      onChange={e => setUsername(e.target.value)}
      placeholder="username" /></p>
    <p className="login-line">
      <input className="login-input"
        value={password}
        onChange={e => setPassword(e.target.value)}
        type={'password'}
        placeholder="password" />
    </p>
    <p className="login-line">
      <input type={'checkbox'} 
      checked={rememberMe} 
      onChange={e => setRememberMe(e.target.checked)}/> Remember Me</p>
    <p className="login-line"><button className="login-button"
      onClick={e => onLogin()}>Log in</button></p>
  </div>
}

export default LoginPage;